#list {
  font-family: "Montserrat", sans-serif;
  position: absolute;
  text-align: left;
  top: 10%;
  left: 25px;
  width: 20%;
  min-width: 250px;
  height: 85%;
  background-color: #FEFFFF;
  overflow: scroll;
  white-space: nowrap;
  box-shadow: 0 0 8px 1px #ccc;
  border-radius: 3px;
  z-index: 3;
  display: flex;
  flex-direction: column;
}

.wrapper {
  border-bottom: #EEE 1px solid;
  max-height: 30px;
  min-height: 30px;
  overflow: hidden;
  transition: max-height 300ms;
  /* The switch - the box around the slider */
  /* Hide default HTML checkbox */
  /* The slider */
  /* Rounded sliders */
}
.wrapper .categoryRow {
  display: flex;
  flex-direction: row;
  height: 30px;
  justify-content: space-between;
  border-bottom: #EEE 1px solid;
}
.wrapper .category::before {
  transform: translateY(-10%) translateX(10%) rotate(-90deg);
}
.wrapper.isOpen {
  max-height: 1000px;
}
.wrapper.isOpen .category::before {
  transform: translateY(10%) translateX(10%);
}
.wrapper.isOpen .category .name {
  font-weight: 600;
}
.wrapper .category {
  border-bottom: #EEE 1px solid;
  font-weight: 400;
  display: flex;
  align-items: center;
}
.wrapper .category::before {
  content: "";
  width: 30px;
  height: 100%;
  background-image: url("../../../assets/arrow.svg");
  transform-origin: center;
  transition: transform 200ms;
}
.wrapper .category .count {
  font-weight: 600;
}
.wrapper .toggle {
  margin: auto;
  padding: 10px;
}
.wrapper .switch {
  position: relative;
  display: inline-block;
  width: 25px;
  height: 12px;
}
.wrapper .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.wrapper .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.wrapper .slider:before {
  position: absolute;
  content: "";
  height: 10px;
  width: 10px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.wrapper input:checked + .slider:before {
  -webkit-transform: translateX(12.5px);
  -ms-transform: translateX(12.5px);
  transform: translateX(12.5px);
}
.wrapper .slider.round {
  border-radius: 34px;
}
.wrapper .slider.round:before {
  border-radius: 50%;
}
.wrapper .body {
  height: 100%;
  overflow: scroll;
}

.toggleStatusOff {
  background-color: #d3d5d5 !important;
}

.category {
  font-size: 9.8887515451px;
  width: 100%;
  overflow: hidden;
  height: 30px;
  cursor: pointer;
}

.listItem, .listItemRow, .buffer-info {
  font-size: 9.8887515451px;
  box-sizing: border-box;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  height: 30px;
  display: flex;
  align-items: center;
}

.buffer-info {
  display: block;
  max-height: 1000px;
  min-height: 50px;
}

.radius, .download {
  padding: 5px 5px 0px 0px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  float: right;
  cursor: pointer;
}
.radius.inactive, .download.inactive {
  pointer-events: none;
}
.radius.inactive span, .radius.inactive div, .download.inactive span, .download.inactive div {
  opacity: 0.2;
}

.downloadIcon {
  padding: 5px;
}
.downloadIcon svg {
  height: 12px;
}
.downloadIcon path {
  stroke: black;
}

.listItem, .categoryRow {
  border-bottom: #EEE 1px solid;
}

.listItemDistance {
  padding: 8px;
}

.listItemIcon {
  width: 30px;
  padding: 5px;
}
.listItemIcon path.map-point, .listItemIcon path.circle {
  opacity: 0;
}

.inBuffer span {
  font-weight: 700;
}
.inBuffer path.map-point {
  opacity: 1;
  fill-opacity: 0;
  stroke-width: 1.5;
}
.inBuffer path.circle {
  opacity: 1;
  stroke-width: 1.5;
}

.selected span {
  font-weight: 700;
}
.selected path.map-point {
  opacity: 1;
  fill-opacity: 1;
  stroke-width: 0;
}
.selected path.circle {
  opacity: 1;
  stroke-width: 0;
}